import React, { Component } from 'react'
import { Form, Input, TreeSelect, Select, } from 'antd';
import { schoolData, subjectData, gradeData } from '../../../api/micro';
export default Form.create()(
    class extends Component {
        state = {
            schoolData: [],
            subjectData: [],
            gradeData: []
        }
        render() {
            const { getFieldDecorator } = this.props.form;
            const formItemLayout = {
                labelCol: {
                    xs: { span: 4 },
                    sm: { span: 4 },
                },
                wrapperCol: {
                    xs: { span: 18 },
                    sm: { span: 18 },
                },
            };

            return (
                <Form  {...formItemLayout}>
                    <Form.Item label="&emsp;&emsp;" >
                        {
                            getFieldDecorator('schoolid', {
                                rules: [{ required: true, message: '!' }],
                            })(<TreeSelect onChange={this.treeChange.bind(this)} placeholder="" treeData={this.state.schoolData} />)}
                    </Form.Item>
                    <Form.Item label="&emsp;&emsp;" >
                        {getFieldDecorator('classes', {
                            rules: [{ required: true, message: '!' }],
                        })(<Select options={this.state.gradeData} placeholder="" >
                            {this.state.gradeData.map(item => (
                                <Select.Option key={item.id} value={item[this.props.formValue['classes']]}>
                                    {item.name}
                                </Select.Option>
                            ))}
                        </Select>)}
                    </Form.Item>
                    <Form.Item label="&emsp;&emsp;" >
                        {getFieldDecorator('subject', {
                            rules: [{ required: true, message: '!' }],
                        })(<Select placeholder="" >
                            {this.state.subjectData.map(item => (
                                <Select.Option key={item.code} value={item[this.props.formValue['subject']]}>
                                    {item.name}
                                </Select.Option>
                            ))}
                        </Select>)}
                    </Form.Item>
                    <Form.Item label="&emsp;&emsp;" >
                        {getFieldDecorator('title', {
                            rules: [{ required: true, message: '!' }],
                        })(<Input placeholder="" />)}
                    </Form.Item>
                </Form>
            )
        }
        componentDidMount() {
            this.getSchool();
            this.getSubject();
        }
        formatSchoolTree(data) {
            return data.map(v => {
                return {
                    title: v.name || '',
                    key: v.id,
                    id: v.id,
                    value: String(v.id),
                    parent: v.parent,
                    isLeaf: (v.children && v.children.length) ? false : true,
                    children: (v.children && v.children.length) ? this.formatSchoolTree(v.children) : []
                }
            })
        }
        formatSelect(data = []) {
            return data.map(item => {
                return {
                    value: item.code,
                    key: item.id,
                    title: item.name
                }
            })
        }
        getSchool() {
            schoolData().then(res => {
                if (res.code === 0) {
                    let data = this.formatSchoolTree(res.data)
                    this.setState({ schoolData: data || [] })
                }

            })
        }
        getSubject() {
            subjectData().then(res => {
                if (res.code === 0) {
                    this.setState({ subjectData: res.data || [] })
                }
            })
        }
        treeChange(value, label, extra) {
            gradeData(value).then(res => {
                if (res.code === 0) {
                    this.setState({ gradeData: res.data || [] })
                }
            })
        }
    }
)



